@import url(https://fonts.googleapis.com/css2?family=Merriweather&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Source+Sans+Pro:wght@400;700&display=swap);
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  background-color: #fff;
}

.login-wrapper {
  height: 100vh;
  width: 80%;
  margin: 0 auto;
  display: flex;
  justify-content: space-around;
  align-items: center;
}

img {
  width: 674px;
  height: 736px;
  object-fit: contain;
}

.form {
  width: 334px;
  margin-top: 48px;
  display: flex;
  flex-direction: column;

  font-family: 'Source Sans Pro', sans-serif;
  color: #000000;
}

.form-row {
  margin-bottom: 36px;
}

.form-label {
  font-style: normal;
  font-weight: normal;
  font-size: 18px;
  line-height: 23px;
}

.form-input {
  border: 1px solid #bcbcbc;
  border-radius: 5px;
  width: 334px;
  height: 48px;
  margin-top: 12px;
  outline: none;

  padding: 0 12px;
  font-style: normal;
  font-weight: normal;
  font-size: 18px;
  line-height: 22px;
  color: #000000;
}

.error {
  margin-top: 12px;
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 18px;
  color: #d62c2c;
}

.imageContainer {
  display: 'flex';
  flex-wrap: 'wrap';
  margin-top: 8px;
  margin-bottom: 12px;
}

.img {
  display: block;
  width: 64px;
  height: 64px;
}

.dropText {
  width: 200px;
  height: 40px;
}

.buttonsRow {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.loginBtn,
.submitBtn {
  background-color: #fd0157;
  border: none;
  border-radius: 5px;
  font-style: normal;
  font-weight: 600;
  font-size: 18px;
  line-height: 23px;
  text-align: center;
  color: #ffffff;
  cursor: pointer;
  outline: none;
}

.loginBtn {
  width: 334px;
  height: 48px;
  margin-top: 13px;
}

.loginBtn:hover,
.submitBtn:hover {
  background-color: #d8064c;
}

.submitBtn {
  width: 179px;
  height: 48px;
}

.cancelBtn {
  width: 132px;
  height: 48px;
  background: #ffffff;
  border: 1px solid #bcbcbc;
  box-sizing: border-box;
  border-radius: 5px;
  cursor: pointer;
  outline: none;

  font-style: normal;
  font-weight: 600;
  font-size: 18px;
  line-height: 23px;
  text-align: center;
  color: #040404;
}

h1 {
  font-family: 'Merriweather', serif;
  font-style: normal;
  font-weight: normal;
  font-size: 48px;
  line-height: 60px;
  color: #000000;
}

.main-wrapper {
  height: 100vh;
  display: flex;
}

.main-content {
  margin: 0 auto;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.addBtn {
  background-color: #fd0157;
  border-radius: 5px;
  border: none;
  width: 333px;
  height: 48px;
  margin-top: 60px;
  outline: none;

  font-family: 'Source Sans Pro', sans-serif;
  font-style: normal;
  font-weight: 600;
  font-size: 18px;
  line-height: 23px;
  text-align: center;
  color: #ffffff;
}

.addBtn:hover {
  background-color: #d8064c;
}

.mainImg {
  position: absolute;
  width: 617px;
  height: 279px;
  left: 93px;
  bottom: 0px;
}

.newMovie-wrapper {
  height: 100vh;
  width: 100%;
  padding: 120px 0 0 120px;
  display: flex;
  flex-direction: column;
}

.newMovieImg {
  width: 526px;
  height: 824px;
  position: absolute;
  right: 0px;
  bottom: 0px;
  top: 113px;
}

.imageContainer {
  display: 'flex';
  flex-wrap: 'wrap';
  margin-top: 8px;
  margin-bottom: 12px;
}

.img {
  display: block;
  width: 64px;
  height: 64px;
}

.movieList-wrapper {
  height: 100vh;
  padding: 120px;
}

table {
  width: 100%;
  margin-top: 50px;
  border-collapse: collapse;
  font-family: 'Source Sans Pro', sans-serif;
}

th {
  font-style: normal;
  font-weight: bold;
  font-size: 18px;
  line-height: 23px;
  color: #000000;
  padding-bottom: 30px;
}

td {
  border-bottom: 1px solid #bcbcbc;
  padding: 20px 0;

  font-style: normal;
  font-weight: normal;
  font-size: 18px;
  line-height: 22px;
  text-align: center;
  color: #000000;
}

th:first-child {
  text-align: left;
}

td:first-child {
  text-align: left;
  padding-left: 15px;
}

th:last-child {
  text-align: right;
  padding-right: 30px;
}

.coverImg {
  height: 60px;
  width: 60px;
  border-radius: 6px;
}

.options {
  text-align: right;
  height: 100%;
}

.optionsContainer {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
}

.editBtn {
  color: #000000;
  padding-right: 45px;
  margin-bottom: 10px;
}

.deleteBtn {
  color: #cf1313;
  padding-right: 35px;
  margin-top: 10px;
}

.editBtn,
.deleteBtn {
  background: transparent;
  border: none;
  outline: none;
  font-style: normal;
  font-weight: normal;
  font-size: 18px;
  line-height: 22px;
  text-align: center;
  -webkit-text-decoration-line: underline;
          text-decoration-line: underline;
  cursor: pointer;
}

.movieList_header {
  display: flex;
  align-items: center;
}

.movieList_btnNew {
  border: none;
  border-bottom: 1px solid currentColor;
  background-color: transparent;
  font-weight: 600;
  font-size: 18px;
  color: #fd0157;
  cursor: pointer;
  padding-bottom: 2px;
  margin-top: 11px;
  margin-left: 40px;
  display: inline-block;
  outline: none;
  transition: all 0.3s;
}

.movieList_btnNew:hover {
  color: #d6044a;
}

.movieList_btnNew span {
  margin-left: 3px;
  transition: all 0.2s;
}

.movieList_btnNew:hover span {
  margin-left: 8px;
}

